<template lang="html">

  <el-row class="video_tool">
    <el-col class="tag">
      <el-tag
        type="success"
        hit
        v-for="tag in tags">
          {{tag}}
        </el-tag>
    </el-col>
    <el-col
      :span="6"
      class="rate-box">
        <span>评分</span>
      <el-rate
        v-model="score"
        allow-half
        class="rate"
        @change="setScore()"
        void-color="#aaa"
        :disabled="disabled"></el-rate>
    </el-col>
    <el-col :span="6">
      <el-button type="primary" size="mini" icon="el-icon-delete"
      :disabled="deleted" @click="delete_video"></el-button>
    </el-col>
  </el-row>
</template>

<script>
export default {
  props: ["id"],
  data() {
    return {
      tags: [],
      score: 0,
      disabled: false,
      deleted: false
    };
  },
  methods: {
    setScore() {
      const res = this.$store.dispatch("setScore", {
        id: this.id,
        score: this.score
      });
      if (res) {
        this.disabled = true;
      }
    },
    getScore() {
      const res = this.$store.dispatch("getScore", this.id);
      if (res.status) {
        this.score = res.score;
        this.disabled = true;
      } else {
        this.disabled = false;
      }
    },
    delete_video() {
      let status = this.$store.dispatch("deleteVideo", this.id);
      if (status) {
        this.$message("删除成功");
        this.$router.push("/");
      } else {
        this.$message({
          message: "删除失败",
          type: "error"
        });
      }
    },
    isDeleted() {
      return this.$store.dispatch("isDelete", this.id);
    }
  },
  mounted() {
    this.getScore();
    this.isDeleted();
  }
};
</script>

<style lang="css">
.video_tool {
  margin-top: 1rem;
  width: 100%;
  padding: 0 1.25rem;
}
.rate-box {
  float: right;
}
.rate {
  display: inline;
}
</style>



