<template lang="html">
  <el-header class="centent">
    <el-button class="el-icon-arrow-left hidden-md-and-up" @click="go_back"></el-button>
    <el-input class="input_box" v-model="tag" placeholder="tag搜索" :sm='8'      prefix-icon="el-icon-search" @change="search(value)"></el-input>
    <el-button type="primary" icon="el-icon-delete" class="hidden-sm-and-down">回收站</el-button>
    <el-button type="primary" @click="goto_home" class="hidden-sm-and-down">首页</el-button>
  </el-header>
</template>

<script>
import "element-ui/lib/theme-chalk/display.css";
export default {
  data() {
    return {
      tag: ""
    };
  },
  methods: {
    goto_home() {
      this.$router.push("/");
    },
    go_back(){
      this.$router.go(-1);
    },
    search(data){
      this.$store.dispatch('searchVideo',data)
    }
  }
};
</script>

<style lang="less" scoped>
.centent {
  top: 0;
  padding-top: 0.63rem;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  height: auto;
  background-color: white;
  text-align: center;
  .input_box {
    max-width: 500px;
    margin-right: 15px;
    width: calc(100% - 100px)
  }
}
</style>



